import { addDoc, collection, dbService, storageService, ref, uploadString, getDownloadURL } from "fBase";
import { v4 as uuidv4 } from 'uuid'; // from https://www.npmjs.com/package/uuid
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTimes } from "@fortawesome/free-solid-svg-icons";

const NweetFactory = ({userObj}) => {
    /// save nweets
    const [nweet, setNweet] = useState("");
    const onSubmit = async (event) => {
        if (nweet === "") {
            return;
          }
        event.preventDefault();
        let attachmentUrl = "";

        if(attachment !== "") {
            const attachmentRef = ref(storageService, `${userObj.uid}/${uuidv4()}`);
            await uploadString(attachmentRef, attachment, "data_url"); // data_url was we used 'readAsDataURL' function.
            attachmentUrl = await getDownloadURL(attachmentRef);
        }
        await addDoc(collection(dbService, "nweets"), {
            text: nweet,
            createdAt: Date.now(),
            creatorId:userObj.uid,
            attachmentUrl,
        });
        setNweet("");
        setAttachment("");
    };
    const onChange = (event) => {
        const {
            target : {value}
        } = event;
        setNweet(value);
    }
    
    const [attachment, setAttachment] = useState("");
    const onFileChange = (event) => {
        const {
            target:{ files },
        } = event;
        const theFile = files[0];
        const reader = new FileReader();
        //add listen event to fileload
        reader.onloadend = (finishedEvent) => {
            const {
               currentTarget : {result},
            } = finishedEvent;
            // finish loading event
            setAttachment(result);
        };
        reader.readAsDataURL(theFile);
    }

    const onClearAttachment = () => setAttachment("");

    return (
        
        <form onSubmit={onSubmit} className="factoryForm">
            <div className="factoryInput__container">
              <input
                className="factoryInput__input"
                value={nweet}
                onChange={onChange}
                type="text"
                placeholder="What's on your mind?"
                maxLength={120}
              />
              <input type="submit" value="&rarr;" className="factoryInput__arrow" />
            </div>
            <label htmlFor="attach-file" className="factoryInput__label">
                <span>Add photos</span>
                <FontAwesomeIcon icon={faPlus} />
            </label>
            <input
                id="attach-file"
                type="file"
                accept="image/*"
                onChange={onFileChange}
                style={{
                opacity: 0,
                }}
            />
            {attachment && (
              <div className="factoryForm__attachment">
                <img
                  src={attachment}
                  style={{
                    backgroundImage: attachment,
                  }}
                />
                <div className="factoryForm__clear" onClick={onClearAttachment}>
                  <span>Remove</span>
                  <FontAwesomeIcon icon={faTimes} />
                </div>
              </div>
            )}
        </form>
    );
};
export default NweetFactory;

